function decodeVIN(vin: string): VINInfo | null {
  if (vin.length !== 17) {
    return null;
  }

  const region = regions[vin[0]] || " ";
  const country = countries[vin[0]] || " ";
  const manufacturer =
    manufacturers[vin.substring(0, 3)] || " ";
  const vehicleAttributesPart =
    vin.substring(3, 8) ||
    "    ";
    const bodyType = vehicleAttributes.bodyType[vehicleAttributesPart[0]] || '  '
    const engineType = vehicleAttributes.engineType[vehicleAttributesPart[1]] || '  '
    const transmissionType = vehicleAttributes.transmissionType[vehicleAttributesPart[2]] || '  '
    const safetySystem = vehicleAttributes.safetySystem[vehicleAttributesPart[3]] || '   '
           
  const checkDigit = vin[8] || "  ";
  const modelYear = modelYears[vin[9]] || " ";
  const assemblyPlant = assemblyPlants[vin[10]] || " ";
  const serialNumber = vin.substring(11) || "  ";

  console.log(
    region,
    country,
    manufacturer,
    vehicleAttributes,
    checkDigit,
    modelYear,
    assemblyPlant,
    serialNumber
  );
  return {
    region,
    country,
    manufacturer,
    vehicleAttributes: `${bodyType}, ${engineType}, ${transmissionType}, ${safetySystem}`,
    checkDigit,
    modelYear,
    assemblyPlant,
    serialNumber,
  };
}
export default decodeVIN;
